.login-container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-form {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
}