@import "reset";
@import "settings/colors";
@import "settings/fonts";
@import "Header";
@import "Menu";
@import "HeaderThreeColumn";
@import "FourSteps";
@import "AboutUs";
@import "WhoWeHelp";
@import "ContactUs";
@import "Footer";
@import "Login";
@import "Logout";

#root {
  width: 100%;
  position: relative;

}
body {
  font-family: $font-base;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}


.container {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.wrapper {
  width: 100%;
}

.decoration {
  background: url("./../assets/Decoration.svg");
  height: 33px;
  width: 253px;
  margin: 20px auto;

}

.half-page {
  width: 50%;
}


.row {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  min-height: 380px;

  .col {
    text-align: center;
    width: 280px;
    color: $color-2;
    p {
      font-weight: lighter;
    }
  }
}

.mb-50{
  margin-bottom: 50px;
}

.border {
  border: 1px solid $color-4;
}